.App {
  text-align: center;
  background-color: #0a0102;
  min-height: 100vmin;
}

.App-logo {
  height: 14vmin;
  pointer-events: none;
  vertical-align: middle;
}

.highlight{
  color: #f598a1;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 00s linear;
  }

  .App-logo:hover {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #0a0102;
  display: flex;
  padding: 20px;
  flex-direction: column;
  align-items: center;
  justify-content: first baseline;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-Body{
  background-color: #0a0102;
  padding: 20px;
  margin-left: 25%;
  text-align: left;
  width: 50%;
  font-size: calc(8px + 1vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
